interface SessionItem {
    [key: string]: any
}

export class Session {
    private static instance: Session
    static get Instance() {
        return this.instance || (this.instance = new Session())
    }

    private items: SessionItem = {}

    /**
     * content session
     */
    content() {
        return this.items;
    }

    /**
     * keys sessionkey
     */
    keys() {
        let keys = []
        for (let item in this.items) {//attr
            keys.push(item)
        }
        return keys
    }

    /**
     * has session
     * @param key 
     */
    has(key: string) {
        return this.items[key] ? true : false
    }

    /**
     * set session
     * @param key 
     * @param value 
     */
    set<T>(key: string, value: T) {
        this.items[key] = value
    }

    /**
     * get session
     * @param key 
     */
    get<T>(key: string) {
        if (this.has(key)) {
            return <T>this.items[key]
        }

        return undefined
    }

    /**
     * del session
     * @param key 
     */
    del(key: string) {
        if (this.has(key)) {
            delete this.items[key]
        }
    }

    /**
     * reset session
     */
    reset() {
        this.items = {}
    }
}

const i = Session.Instance
export default i


